import {initDB, INIT_DB} from '../store/db'
import {fromJS} from 'immutable'
import {expect} from 'chai'

describe('actions', () => {
  it('should create an action to add a db', () => {
    const db = fromJS({users: {name: {type: 'string', allowNull: false }}})

    const why = fromJS(['1', '2'])
    console.log(db, why)
    const expectedAction = fromJS({
      type: INIT_DB,
      db: db
    })
    const actualAction = initDB(db)
    console.log(actualAction)
    expect(expectedAction.equals(actualAction)).to.equal(true)
  })
})



